<template>
  <md-dialog
    @md-opened="init"
    :md-active="show"
    :md-fullscreen="false"
    @update:mdActive="close => (!close ? $emit('close') : '')"
  >
    <Loader v-if="loading" />
    <md-dialog-title>Подключённые устройства</md-dialog-title>
    <div class="devices">
      <div
        v-for="user of users"
        :key="user.id"
        class="device"
        :class="{
          full: user.limited === false,
          current: uid == user.id,
          'no-menu': !full || uid == user.id || user.limited === false
        }"
      >
        <md-icon>{{ user.cdevice.img }}</md-icon>
        <div class="info">
          <span class="md-body-1">
            {{ user.cdevice.platform }} | {{ user.cdevice.os }}
          </span>
          <span class="md-caption">
            {{ user.cdevice.browser }} | {{ user.cdevice.version }}
          </span>
        </div>
        <md-menu md-size="auto" md-direction="bottom-end">
          <md-button class="md-icon-button" md-menu-trigger>
            <md-icon>menu</md-icon>
          </md-button>

          <md-menu-content>
            <md-menu-item @click="ChangeRestric(user.id)">
              <span>Полный доступ</span>
              <md-icon>grade</md-icon>
            </md-menu-item>

            <md-menu-item @click="deleteUser(user.id)">
              <span>Удалить</span>
              <md-icon>clear</md-icon>
            </md-menu-item>
          </md-menu-content>
        </md-menu>
      </div>
    </div>
  </md-dialog>
</template>

<script>
import { mapState, mapActions } from "vuex";
import Loader from "@/components/Loader";

export default {
  props: ["show"],
  data: () => ({
    full: false,
    users: [],
    loading: true
  }),
  computed: mapState({
    uid: state => state.settings.storage.uid
  }),
  methods: {
    ...mapActions([
      "RemoveConnectedUser",
      "ChangeRestric",
      "CheckRights",
      "ConnectedUsers"
    ]),
    init() {
      this.loading = true;
      this.CheckRights()
        .then(can => (this.full = can))
        .then(() => this.ConnectedUsers())
        .then(users => (this.users = users))
        .then(() =>
          this.users.sort(
            (a, b) =>
              new Date(a.ctime) * (0 - (a.id == this.uid)) -
              new Date(b.ctime) * (0 - (b.id == this.uid))
          )
        )
        .catch(err => this.$root.$emit("catch-error", err))
        .finally(() => (this.loading = false));
    },
    ChangeRestric(id) {
      this.loading = true;
      this.ChangeRestric(id)
        .then(limited => (this.users.find(u => u.id == id).limited = limited))
        .catch(err => this.$root.$emit("catch-error", err))
        .finally(() => (this.loading = false));
    },
    deleteUser(id) {
      this.loading = true;
      this.RemoveConnectedUser(id)
        .then(() =>
          this.users.splice(
            this.users.findIndex(u => u.id == id),
            1
          )
        )
        .catch(err => this.$root.$emit("catch-error", err))
        .finally(() => (this.loading = false));
    }
  },
  components: {
    Loader
  }
};
</script>

<style>
div.devices {
  display: flex;
  padding: 0 24px 24px;
  flex-direction: column;
}
div.devices > div.device:not(:first-child) {
  border-top: 1px solid rgba(0, 0, 0, 0.12);
}
div.devices > div.device {
  display: flex;
  padding: 4px 0;
  align-items: center;
  flex-direction: row;
}
div.devices > div.device > i.md-icon {
  margin: 0;
  width: 30px;
  height: 30px;
  min-width: 30px;
  font-size: 30px !important;
}
div.devices > div.device > div.info {
  padding: 0;
  display: flex;
  margin: 0 8px;
  overflow: hidden;
  flex-direction: column;
}
div.devices > div.device > div.info > span {
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
div.devices > div.device > div.md-menu {
  margin-left: auto;
}
div.devices > div.device.no-menu > div.md-menu {
  display: none;
}
div.devices > div.device.full > i.md-icon,
div.devices > div.device.full > div.info > span {
  color: var(--md-theme-default-primary) !important;
}
div.devices > div.device.current > i.md-icon,
div.devices > div.device.current > div.info > span {
  color: var(--md-theme-default-accent) !important;
}
div.devices > div.device.current > div.info > span:last-child {
  opacity: 0.7;
}
div.content.Настройки ~ .md-menu-content {
  z-index: 12;
}
</style>
