<template>
  <div class="birthday">
    <transition name="fade">
      <md-progress-bar
        v-if="loading"
        md-mode="query"
        class="loader"
        key="1"
      ></md-progress-bar>
    </transition>
    <md-field>
      <label>Месяц</label>
      <md-select
        v-model="selected"
        :disabled="loading"
        @md-selected="loadBirthdays"
      >
        <md-option
          :key="month.name"
          v-for="month in months"
          :value="month.date.toJSON()"
          >{{ month.name }}</md-option
        >
      </md-select>
    </md-field>
    <transition name="fade" mode="out-in">
      <md-table
        md-card
        v-model="birthdays"
        v-if="birthdays.length"
        class="result md-scrollbar"
      >
        <md-table-row slot="md-table-row" slot-scope="{ item }">
          <md-table-cell md-label="Дата">{{ item.date | date }}</md-table-cell>
          <md-table-cell md-label="Имя">{{ item.name }}</md-table-cell>
          <md-table-cell md-label="Роль">{{ item.role }}</md-table-cell>
          <md-table-cell md-label="Класс">{{ item.class }}</md-table-cell>
        </md-table-row>
      </md-table>
    </transition>
  </div>
</template>

<script>
const month = [
  "Январь",
  "Февраль",
  "Март",
  "Апрель",
  "Май",
  "Июнь",
  "Июль",
  "Август",
  "Сентябрь",
  "Октябрь",
  "Ноябрь",
  "Декабрь"
];

export default {
  data: () => ({
    loading: true,
    months: [],
    selected: null,
    birthdays: []
  }),
  watch: {
    selected(value, old) {
      if (this.loading || !old) return;
      this.$store.dispatch("saveCache", {
        name: "SelectedMonth",
        value
      });
    }
  },
  methods: {
    initView() {
      this.loading = true;
      this.birthday = [];

      this.$store
        .dispatch("GetStudyYear")
        .then(({ start, end }) => {
          const now = new Date();
          start = new Date(start);
          end = new Date(end);
          while (start < end) {
            this.months.push({
              name: month[start.getMonth()],
              date: new Date(start)
            });
            start.setMonth(start.getMonth() + 1);
          }
          return this.months
            .find(m => m.date.getMonth() == now.getMonth())
            .date.toJSON();
        })
        .then(index =>
          this.$store.dispatch("getCache", {
            time: 12e4,
            name: "SelectedMonth",
            next: () => index
          })
        )
        .then(index => {
          this.selected = index;
          this.loading = false;
          this.loadBirthdays();
        })
        .catch(err => this.$root.$emit("catch-error", err));
    },
    loadBirthdays() {
      this.loading = true;

      return this.$store
        .dispatch("GetBirthdays", this.selected)
        .then(birthdays => (this.birthdays = birthdays))
        .catch(err => this.$root.$emit("catch-error", err))
        .finally(() => (this.loading = false));
    }
  },
  mounted() {
    this.$root.$on("user-changed", this.initView);
    this.initView();
  }
};
</script>

<style>
.birthday {
  position: relative;
}
.birthday > .loader {
  top: 0;
  left: 0;
  width: 100%;
  position: absolute;
}
.birthday > .result {
  display: flex;
  flex-wrap: wrap;
  margin-top: 20px;
  justify-content: space-around;
}
.birthday > .result > .md-card {
  margin: 10px;
  flex-basis: 550px;
}

.birthday .fade-enter-active,
.birthday .fade-leave-active {
  overflow: visible !important;
  max-height: 100vh !important;
  transition: all 450ms ease-in-out !important;
}
.birthday .fade-enter,
.birthday .fade-leave-to {
  margin: 0 !important;
  opacity: 0 !important;
  padding: 0 !important;
  max-height: 0 !important;
  transform: scale(0) translateY(100%) !important;
}
</style>
