<template>
  <div class="diary-lesson" :class="{ stripes }">
    <span class="number md-body-1">{{ number }}</span>
    <div class="info">
      <span class="md-body-2">{{ name }}</span>
      <span class="md-caption"
        >{{ startTime }} - {{ endTime }}, {{ room }}
      </span>
      <span
        class="homework md-body-1"
        @click="e => e.target.classList.toggle('show')"
        >{{ homework }}</span
      >
    </div>
    <div class="assignments">
      <router-link
        tag="span"
        :key="assignment.id"
        v-for="assignment in assignments"
        :class="{ bad: assignment.mark <= 3 }"
        :to="{
          name: 'Show mark',
          params: {
            id: assignment.id,
            mark: assignment.mark,
            type: assignment.type
          }
        }"
        >{{ assignment.mark }}</router-link
      >
    </div>
  </div>
</template>

<script>
export default {
  props: [
    "name",
    "room",
    "number",
    "endTime",
    "homework",
    "startTime",
    "assignments"
  ],
  computed: {
    stripes() {
      return this.$store.state.settings.storage.diaryStripes;
    }
  }
};
</script>

<style>
.diary-lesson {
  display: flex;
  padding: 5px 0;
  flex-direction: row;
}
.diary-lesson.stripes:not(:first-child) {
  border-top: 1px solid rgba(0, 0, 0, 0.12);
}

.diary-lesson > .number {
  width: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.diary-lesson > .info {
  width: calc(100% - 80px);
  margin: 0 10px;
  display: flex;
  flex-direction: column;
}
.diary-lesson > .info > .md-body-2 {
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.diary-lesson > .info > .md-caption {
  line-height: 10px;
  margin-bottom: 2px;
}
.diary-lesson > .info > .homework {
  min-height: 20px;
}
.diary-lesson > .info > .homework:not(.show) {
  cursor: pointer;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.diary-lesson > .assignments {
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.diary-lesson > .assignments > span {
  cursor: pointer;
  padding: 2px 4px;
}
.diary-lesson > .assignments > span.bad {
  font-size: 16px;
  font-weight: bold;
}
</style>
