<template>
  <div class="grade">
    <h3>Четвертные</h3>
    <Loader v-if="loading" />
    <div v-show="!loading" id="report" v-html="totalMarks"></div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import Loader from "@/components/Loader.vue";

export default {
  data: () => ({
    loading: true,
    totalMarks: ""
  }),
  methods: mapActions(["GetTotalReport"]),
  created() {
    this.GetTotalReport()
      .then(html => ((this.totalMarks = html), (this.loading = false)))
      .catch(err => {
        this.loading = false;
        if (err.code == 4008) {
          // eslint-disable-next-line no-undef
          M.toast({ html: "Сайт лег, попробуй позже" });
        } else if (err.code == 4006) {
          // eslint-disable-next-line no-undef
          M.toast({ html: "Ошибка парсера, попробуй ещё раз" });
        } else {
          // eslint-disable-next-line no-undef
          M.toast({ html: "Неизвестная ошибка, попробуй ещё раз" });
        }
        console.error(err);
      });
  },
  components: {
    Loader
  }
};
</script>
