<template>
  <div>
    <transition name="fade" mode="out-in">
      <component :is="layout" v-if="!loading">
        <router-view />
      </component>
    </transition>
    <md-snackbar
      :md-active="!!alert"
      @update:mdActive="
        v => {
          if (!v) alert = false;
        }
      "
    >
      <span>{{ alert }}</span>
    </md-snackbar>
  </div>
</template>

<script>
import { mapState } from "vuex";
import InfoLayout from "@/layouts/InfoLayout.vue";
import MainLayout from "@/layouts/MainLayout.vue";

export default {
  data: () => ({
    alert: false,
    loading: true
  }),
  computed: {
    ...mapState({
      settings: state => state.settings.storage,
      connectedUsers: state => state.settings.storage.connectedUsers
    }),
    uid: {
      get() {
        return this.$store.state.settings.storage.uid;
      },
      set(val) {
        this.changeUser(val);
        return true;
      }
    },
    theme() {
      return this.settings.darkMode ? "dark" : "light";
    },
    layout() {
      if (!this.$route.meta?.layout) return "div";
      return this.$route.meta?.layout + "-layout";
    },
    needAuth() {
      return this.$route.meta?.auth ?? false;
    }
  },
  watch: {
    settings: {
      handler() {
        this.$store.dispatch("saveSettings");
      },
      deep: true
    },
    needAuth(val) {
      if (val && this.connectedUsers.length == 0) {
        this.$router.replace("/login");
        this.alert = "Сначала нужно войти!";
      }
    }
  },
  methods: {
    checkExist() {
      function makeFetch(users = [], i = 0) {
        return i < users.length
          ? fetch(`//${location.hostname}/exist/${users[i]}`)
              .then(res => res.json())
              .then(exist => {
                if (!exist) users.splice(i, 1);
                else i++;
              })
              .finally(() => makeFetch(users, i))
          : new Promise(res => res(void 0));
      }
      return makeFetch(this.connectedUsers).then(() => {
        if (this.connectedUsers.length == 0) return false;
        if (!this.connectedUsers.includes(this.uid)) {
          this.uid = this.connectedUsers[0];
        }
        return true;
      });
    },
    catchError(err) {
      switch (err.code) {
        case 4001:
          this.alert = "Не удалось понять запрос";
          break;
        case 4002:
          this.alert = "Запрос построен неверно";
          break;
        case 4004:
          this.alert = "Неизвестный метод";
          break;
        case 4003:
        case 4005:
          this.alert = err.msg;
          break;
        case 4006:
          this.alert = "Ошибка парсера, попробуйте еще раз";
          break;
        case 4007:
          this.alert = "Для этого метода нужна авторизация в сетевом";
          break;
        case 4008:
          this.alert = "Сетевой город не отвечает";
          break;
        case 4009:
          this.$router.replace("/login");
          this.alert = "Для начала нужно войти в систему";
          break;
        default:
          this.alert = "Неизвестная ошибка, попробуйте ещё раз";
      }
      console.error(err);
    },
    changeUser(id) {
      this.$store
        .dispatch("ChangeUser", id)
        .then(() => {
          this.$store.state.settings.storage.uid = id;
          setTimeout(() => this.$root.$emit("user-changed"), 0);
        })
        .catch(err => this.catchError(err));
    },
    changeTheme() {
      this.settings.darkMode = !this.settings.darkMode;
      this.$material.theming.theme = this.theme;
      console.log("Theme was changed!");
    }
  },
  components: {
    InfoLayout,
    MainLayout
  },
  mounted() {
    this.$root.$on("change-theme", this.changeTheme);
    this.$root.$on("change-user", this.changeUser);
    this.$root.$on("catch-error", this.catchError);
    this.$root.$on("show-alert", t => (this.alert = t));
    this.$material.theming.theme = this.theme;
    this.$connect(`//${location.hostname}/?uid=${this.uid}`);
    this.$store.dispatch("initCache");

    this.loading = true;
    this.alert = "⏳ Проверка пользователей...";
    this.checkExist()
      .catch(err => this.catchError(err))
      .finally(() => {
        this.alert = "✅ Пользователи проверены!";
        if (this.needAuth && this.connectedUsers.length == 0) {
          this.$router.replace("/login");
          this.alert = "Сначала нужно войти!";
        }
        this.loading = false;
      });
  }
};
</script>

<style>
.fade-enter-active,
.fade-leave-active {
  transition: all 350ms ease !important;
}
.fade-enter,
.fade-leave-to {
  opacity: 0 !important;
}
</style>

<style lang="scss">
@import "~vue-material/dist/theme/engine";
@include md-register-theme(
  "light",
  (
    "text-primary-on-background-variant": rgba(0, 0, 0, 0.87),
    theme: light
  )
);
@include md-register-theme(
  "dark",
  (
    "primary-on-background": #2eb0f7,
    "background-variant": black,
    steppervariant: #ffd800,
    fieldvariant: #ffd800,
    primary: #058ed9,
    accent: #f39237,
    theme: dark
  )
);
@import "~vue-material/dist/theme/all";
</style>
