<template>
  <div class="main-layout">
    <Toolbar :title="title" />
    <transition name="fade" mode="out-in">
      <router-view class="content" />
    </transition>
    <Bottombar v-if="showBottombar" />
  </div>
</template>

<script>
import Toolbar from "../components/Toolbar";
import Bottombar from "../components/Bottombar";

export default {
  computed: {
    title() {
      return this.$route.meta?.title;
    },
    showBottombar() {
      return this.$route.meta?.bottombar ?? true;
    }
  },
  components: {
    Toolbar,
    Bottombar
  }
};
</script>

<style>
.main-layout > .content {
  width: 100%;
  padding: 2px 14px;
  overflow: hidden;
  padding-bottom: 56px;
}
</style>
