<template>
  <md-dialog
    :md-active="!!id"
    class="mark-viewer"
    :md-fullscreen="false"
    @md-clicked-outside="$router.replace('/diary')"
  >
    <md-dialog-title>Информация о задании</md-dialog-title>

    <Loader :show="loading" />

    <transition name="fade">
      <div v-if="showResult" :class="{ stripes }" class="info">
        <div>
          <div class="md-body-2">Дата:</div>
          <div class="md-body-1">{{ date | date }}</div>
        </div>
        <div>
          <div class="md-body-2">Вес:</div>
          <div class="md-body-1">{{ weight }}</div>
        </div>
        <div>
          <div class="md-body-2">Оценка:</div>
          <div class="md-body-1">{{ mark }}</div>
        </div>
        <div>
          <div class="md-body-2">Учитель:</div>
          <div class="md-body-1" @click="showCell">{{ teacher }}</div>
        </div>
        <div>
          <div class="md-body-2">Предмет:</div>
          <div class="md-body-1" @click="showCell">{{ subject.name }}</div>
        </div>
        <div>
          <div class="md-body-2">Тип задания:</div>
          <div class="md-body-1" @click="showCell">{{ assignmentType }}</div>
        </div>
        <div>
          <div class="md-body-2">Тема задания:</div>
          <div class="md-body-1" @click="showCell">{{ theme }}</div>
        </div>
        <router-link
          tag="md-button"
          class="md-accent"
          :to="{ name: 'Show subject', params: { id: subject.id } }"
        >
          <md-icon>launch</md-icon>
          Отчет об успеваемости</router-link
        >
      </div>
    </transition>
  </md-dialog>
</template>

<script>
import Loader from "@/components/Loader";

export default {
  props: {
    id: {
      type: Number,
      default: null
    },
    mark: {
      type: Number,
      default: null
    },
    type: {
      type: Number,
      default: null
    }
  },
  data: () => ({
    loading: true,
    date: null,
    theme: "",
    weight: null,
    teacher: "",
    subject: {
      id: null,
      name: null
    },
    allTypes: []
  }),
  watch: {
    id() {
      this.loadMark();
    }
  },
  computed: {
    stripes() {
      return this.$store.state.settings.storage.diaryStripes;
    },
    showResult() {
      return !this.loading && this.date;
    },
    assignmentType() {
      return this.allTypes.find(t => t.id == this.type)?.name ?? "";
    }
  },
  methods: {
    loadMark() {
      this.loading = true;
      if (!this.id) return;
      this.$store
        .dispatch("GetAssignment", this.id)
        .then(assignment => {
          this.date = new Date(assignment.date);
          this.theme = assignment.theme;
          this.weight = assignment.weight;
          this.teacher = assignment.teacher;
          this.subject.id = assignment.subject.id;
          this.subject.name = assignment.subject.name;
        })
        .catch(err => this.$root.$emit("catch-error", err))
        .finally(() => (this.loading = false));
    },
    showCell(e) {
      e.target.classList.toggle("show");
    }
  },
  mounted() {
    this.$store
      .dispatch("GetAssignmentsType")
      .then(types => (this.allTypes = types))
      .then(() => this.loadMark());
  },
  components: {
    Loader
  }
};
</script>

<style>
.mark-viewer {
  z-index: 6 !important;
  margin: 0 auto;
  max-width: 500px;
}

.mark-viewer .fade-enter-active,
.mark-viewer .fade-leave-active {
  max-width: 500px !important;
  max-height: 400px !important;
  transition: all 350ms ease !important;
}
.mark-viewer .fade-enter,
.mark-viewer .fade-leave-to {
  opacity: 0 !important;
  padding: 0 !important;
  max-width: 0 !important;
  max-height: 0 !important;
}

.mark-viewer div.info {
  padding: 6px 24px 24px;
}
.mark-viewer div.info > div {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.mark-viewer div.info > div {
  padding-top: 5px;
  padding-bottom: 5px;
  border-top: 1px solid rgba(0, 0, 0, 0.12);
}
.mark-viewer div.info > div:first-child,
.mark-viewer div.info:not(.stripes) > div {
  border-width: 0;
}
.mark-viewer div.info > div > div:first-child {
  margin-right: 8px;
  white-space: nowrap;
}
.mark-viewer div.info > div > div:last-child {
  width: 100%;
  cursor: pointer;
  overflow: hidden;
  line-height: 24px;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.mark-viewer div.info > div > div.show {
  white-space: normal;
}
</style>
