<template>
  <div class="journal">
    <transition name="fade">
      <md-progress-bar
        v-if="showLoader"
        md-mode="query"
        class="loader"
      ></md-progress-bar>
    </transition>
    <dir class="input-range">
      <md-datepicker v-model="journal.range.start" />
      <md-datepicker v-model="journal.range.end" />
    </dir>
    <md-button
      @click="loadJournal"
      :disabled="disabledInput"
      class="md-raised md-primary"
      >Загрузить</md-button
    >
    <transition name="fade">
      <md-list class="result" v-if="journal.result.length">
        <md-list-item
          v-for="subject in journal.result"
          :key="subject.name"
          md-expand
        >
          <span class="md-list-item-text"
            >({{ subject.middleMark }}) {{ subject.name }}</span
          >

          <md-list slot="md-expand">
            <md-list-item class="md-inset"
              >Средний балл: {{ subject.middleMark }}</md-list-item
            >
            <md-list-item
              v-for="assignment in sort(subject.assignments)"
              :key="assignment.value + assignment.date"
              class="md-inset"
              >{{ assignment.date | date }}:
              {{ assignment.value }}</md-list-item
            >
          </md-list>
        </md-list-item>
      </md-list>
    </transition>
  </div>
</template>

<script>
export default {
  data: () => ({
    loading: false,
    studyYear: {
      start: null,
      end: null
    },
    journal: {
      loading: false,
      range: {
        start: null,
        end: null
      },
      result: []
    }
  }),
  computed: {
    showLoader() {
      return this.loading || this.journal.loading;
    },
    disabledInput() {
      return this.loading || this.journal.loading;
    }
  },
  watch: {
    "journal.range": {
      handler({ start, end }) {
        this.$store.state.settings.storage.journalRange.start = start;
        this.$store.state.settings.storage.journalRange.end = end;
      },
      deep: true
    }
  },
  methods: {
    initView() {
      this.loading = true;
      this.journal.result = [];
      this.studyYear.end = null;
      this.studyYear.start = null;
      this.journal.loading = false;
      this.journal.range.end = null;
      this.journal.range.start = null;

      return this.$store
        .dispatch("GetStudyYear")
        .then(({ start, end }) => {
          this.studyYear.start = new Date(start);
          this.studyYear.end = new Date(end);

          return this.$store.state.settings.storage.journalRange;
        })
        .then(({ start, end }) => {
          this.journal.range.start = new Date(start ?? this.studyYear.start);
          this.journal.range.end = new Date(end ?? this.studyYear.end);
          this.loading = false;
        })
        .catch(err => this.$root.$emit("catch-error", err));
    },
    sort(assignments) {
      return [...assignments].sort(
        (a, b) => -new Date(b.date) - new Date(a.date)
      );
    },
    loadJournal() {
      this.journal.loading = true;

      this.$store
        .dispatch("GetStudyReport", { ...this.journal.range })
        .then(journal => (this.journal.result = journal))
        .catch(err => this.$root.$emit("catch-error", err))
        .finally(() => (this.journal.loading = false));
    }
  },
  filters: {
    date(str) {
      const date = new Date(str);
      const month = [
        "января",
        "февраля",
        "марта",
        "апреля",
        "мая",
        "июня",
        "июля",
        "августа",
        "сентября",
        "октября",
        "ноября",
        "декабря"
      ];

      return (
        date.getDate() +
        " " +
        month[date.getMonth()] +
        " " +
        (new Date().getFullYear() != date.getFullYear()
          ? date.getFullYear()
          : "")
      );
    }
  },
  mounted() {
    this.$root.$on("user-changed", this.initView);
    this.initView();
  }
};
</script>

<style>
.journal {
  position: relative;
}

.journal > .loader {
  top: 0;
  left: 0;
  width: 100%;
  position: absolute;
}

.journal > .input-range {
  padding: 0;
  width: 400px;
  display: flex;
  max-width: 100%;
  flex-direction: row;
  justify-content: space-between;
}
.journal > .input-range > .md-datepicker {
  width: 45%;
  overflow: hidden;
}

.journal .fade-enter-active,
.journal .fade-leave-active {
  overflow: visible !important;
  max-height: 76px !important;
  transition: all 350ms ease-in-out !important;
}
.journal .fade-enter,
.journal .fade-leave-to {
  margin: 0 !important;
  opacity: 0 !important;
  padding: 0 !important;
  max-height: 0 !important;
  transform: scale(0) translateY(100%) !important;
}

.journal > .result {
  width: 600px;
  max-width: 100%;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
}
</style>
