<template>
  <div class="announcements">
    <transition name="fade">
      <md-progress-bar
        v-if="loading"
        md-mode="query"
        class="loader"
        key="1"
      ></md-progress-bar>
    </transition>
    <transition-group tag="div" name="fade" mode="out-in" class="result">
      <md-empty-state
        key="empty"
        v-if="!announcements.length && !loading"
        class="md-accent"
        md-icon="inbox"
        md-label="Ничего нет"
        md-description="Объявления ещё не размещены"
      >
      </md-empty-state>

      <md-card
        v-show="!loading"
        v-for="announcement in announcements"
        :key="announcement.id"
      >
        <md-card-header>
          <div class="md-title">{{ announcement.name }}</div>
          <div class="md-caption">
            {{ announcement.author }}, {{ announcement.date | date }}
            {{ announcement.date | time }}
          </div>
        </md-card-header>

        <md-card-content v-html="announcement.description"></md-card-content>
      </md-card>
    </transition-group>
  </div>
</template>

<script>
export default {
  data: () => ({
    loading: true,
    announcements: []
  }),
  methods: {
    initView() {
      this.loading = true;
      this.announcements = [];

      this.$store
        .dispatch("GetAnnouncements")
        .then(announcements => {
          this.announcements = announcements;
          this.loading = false;
          this.MarkRead();
        })
        .catch(err => this.$root.$emit("catch-error", err));
    },
    MarkRead() {
      const alredyRead = this.$store.state.settings.storage.readAnnouncements;
      this.announcements.forEach(({ id }) => {
        if (alredyRead.includes(id)) return;
        alredyRead.push(id);
      });
    }
  },
  filters: {
    time(val) {
      if (!val) return "";
      if (typeof val == "string") val = new Date(val);
      function num(val) {
        return `${val < 10 ? "0" : ""}${val}`;
      }
      return `${val.getHours()}:${num(val.getMinutes())}`;
    }
  },
  mounted() {
    this.$root.$on("user-changed", this.initView);
    this.initView();
  }
};
</script>

<style>
.announcements {
  position: relative;
}
.announcements > .loader {
  top: 0;
  left: 0;
  width: 100%;
  position: absolute;
}
.announcements > .result {
  display: flex;
  flex-wrap: wrap;
  padding-top: 20px;
  justify-content: space-around;
}
.announcements > .result > .md-card {
  margin: 10px;
  flex-basis: 550px;
}

.announcements .fade-enter-active,
.announcements .fade-leave-active {
  overflow: visible !important;
  max-height: 100vh !important;
  transition: all 450ms ease-in-out !important;
}
.announcements .fade-enter,
.announcements .fade-leave-to {
  margin: 0 !important;
  opacity: 0 !important;
  padding: 0 !important;
  max-height: 0 !important;
  transform: scale(0) translateY(100%) !important;
}
</style>
