<template>
  <md-dialog
    :md-active="show"
    :md-fullscreen="false"
    @update:mdActive="close => (!close ? $emit('close') : '')"
  >
    <md-dialog-title>
      {{ loading ? "Загрузка..." : "Добавить устройство" }}
    </md-dialog-title>

    <Loader v-if="loading" />

    <qrcode-vue
      size="200"
      level="H"
      class="code"
      :value="link"
      renderAs="svg"
      v-if="!loading"
    ></qrcode-vue>

    <md-field class="input-link">
      <label>Скопировать ссылку</label>
      <md-input :value="link" @click="copy" readonly></md-input>
      <span class="md-helper-text">Ссылка действует в течении 30 минут</span>
    </md-field>
  </md-dialog>
</template>

<script>
import { mapActions } from "vuex";
import QrcodeVue from "qrcode.vue";
import Loader from "@/components/Loader";

export default {
  props: ["id"],
  data: () => ({
    link: "",
    show: false,
    loading: true
  }),
  watch: {
    id() {
      this.loadLink();
    }
  },
  methods: {
    ...mapActions(["GetAuthLink"]),
    loadLink() {
      if (!this.id) return (this.show = false);
      this.show = true;
      this.loading = true;
      this.GetAuthLink(this.id)
        .then(link => (this.link = link))
        .catch(err => {
          this.show = false;
          this.$root.$emit("catch-error", err);
        })
        .finally(() => (this.loading = false));
    },
    copy(e) {
      e.target.select();
      document.execCommand("copy");
    }
  },
  mounted() {
    this.loadLink();
  },
  components: {
    Loader,
    QrcodeVue
  }
};
</script>

<style scoped>
div.code {
  display: flex;
  padding: 10px 50px;
  align-items: center;
  flex-direction: column;
}
div.input-link {
  max-width: 80%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 40px;
}
</style>
