<template>
  <div class="settings">
    <md-list
      class="md-layout-item md-xsmall-size-100 md-small-size-60 md-medium-size-40 md-large-size-30 md-xlarge-size-20"
    >
      <md-subheader>Авторизация</md-subheader>

      <md-list-item v-if="canShare" @click="showDevices = true">
        <md-icon>devices</md-icon>
        <span class="md-list-item-text">Подключённые устройства</span>
      </md-list-item>
      <md-list-item v-if="canShare" @click="qrID = uid">
        <md-icon>qr_code</md-icon>
        <span class="md-list-item-text">Добавить устройство</span>
      </md-list-item>
      <md-list-item @click="exit">
        <md-icon>exit_to_app</md-icon>
        <span class="md-list-item-text">Выйти с устройства</span>
      </md-list-item>
    </md-list>

    <md-list
      class="md-layout-item md-xsmall-size-100 md-small-size-60 md-medium-size-40 md-large-size-30 md-xlarge-size-20"
    >
      <md-subheader>Дневник</md-subheader>

      <md-list-item>
        <md-icon>view_headline</md-icon>
        <span class="md-list-item-text">Разделение предметов</span>
        <md-switch v-model="settings.diaryStripes" />
      </md-list-item>

      <md-list-item>
        <md-icon>view_week</md-icon>
        <span class="md-list-item-text">Суббота конец недели</span>
        <md-switch v-model="settings.noIncludeSunday" />
      </md-list-item>

      <md-list-item>
        <md-icon>access_time</md-icon>
        <span class="md-list-item-text">Ожидание нажатия</span>
        <md-menu md-direction="bottom-end" md-close-on-select>
          <md-button md-menu-trigger
            >{{ ~~(settings.diaryWaitTime / 1000) }}s</md-button
          >

          <md-menu-content>
            <md-menu-item @click="settings.diaryWaitTime = 5000"
              >5s</md-menu-item
            >
            <md-menu-item @click="settings.diaryWaitTime = 4000"
              >4s</md-menu-item
            >
            <md-menu-item @click="settings.diaryWaitTime = 3000"
              >3s</md-menu-item
            >
            <md-menu-item @click="settings.diaryWaitTime = 2000"
              >2s</md-menu-item
            >
            <md-menu-item @click="settings.diaryWaitTime = 1000"
              >1s</md-menu-item
            >
          </md-menu-content>
        </md-menu>
      </md-list-item>
    </md-list>

    <QRcode :id="qrID" @close="qrID = null" />
    <Devices :show="showDevices" @close="showDevices = false" />
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import QRcode from "@/components/QRcode";
import Devices from "@/components/ConnectedDevices";

export default {
  data: () => ({
    qrID: null,
    canShare: false,
    showDevices: false
  }),
  computed: {
    ...mapState({
      settings: state => state.settings.storage,
      connectedUsers: state => state.settings.storage.connectedUsers
    }),
    uid: {
      get() {
        return this.$store.state.settings.storage.uid;
      },
      set(val) {
        this.$store.state.settings.storage.uid = val;
        return true;
      }
    }
  },
  methods: {
    ...mapActions(["CheckRights", "RemoveCurrentUser"]),
    exit() {
      this.RemoveCurrentUser(this.uid)
        .then(() => {
          const i = this.connectedUsers.findIndex(u => u == this.uid);
          this.connectedUsers.splice(i, 1);
          this.uid = this.connectedUsers[0];
          if (this.connectedUsers.length) this.$router.replace("/settings");
          else this.$router.replace("/login");
        })
        .catch(err => this.$root.$emit("catch-error", err));
    }
  },
  mounted() {
    this.CheckRights()
      .then(can => (this.CheckRights = can))
      .catch(err => this.$root.$emit("catch-error", err));
  },
  components: {
    QRcode,
    Devices
  }
};
</script>

<style>
div.settings > ul.md-list {
  background-color: transparent !important;
}
</style>
