<template>
  <div class="diary">
    <transition name="fade" mode="out-in">
      <md-progress-bar
        v-if="showLoader"
        md-mode="query"
        class="loader"
        key="1"
      ></md-progress-bar>
      <md-progress-bar
        :style="{ '--time': countdown.time + 'ms' }"
        :md-value="100 - countdown.progress"
        v-else-if="countdown.work"
        md-mode="determinate"
        class="countdown"
        key="2"
      ></md-progress-bar>
    </transition>
    <div class="select-week">
      <md-field>
        <label>Неделя</label>
        <md-select
          v-model="week.index"
          :disabled="disableInput"
          @md-selected="countdown.activate(waitTime).then(() => loadWeek())"
        >
          <md-option
            v-for="(range, index) in week.ranges"
            :value="index"
            :key="index"
            >{{ range.start | date }} - {{ range.end | date }}</md-option
          >
        </md-select>
      </md-field>
      <md-button
        class="md-primary"
        :disabled="disableInput"
        @click="week.index--"
      >
        <md-icon>navigate_before</md-icon>
      </md-button>
      <md-button
        class="md-primary"
        :disabled="disableInput"
        @click="week.index++"
      >
        <md-icon>navigate_next</md-icon>
      </md-button>
    </div>
    <transition-group name="fade" tag="div" mode="out-in" class="result">
      <Day v-for="day in week.result" :key="day.date" :day="day" />
    </transition-group>
    <Mark :id="mark.id" :type="mark.type" :mark="mark.mark" />
  </div>
</template>

<script>
import Day from "../components/DiaryDay";
import Mark from "../components/ViewMark";

export default {
  data: () => ({
    loading: true,
    week: {
      _index: 0,
      loading: false,
      get index() {
        return this._index ?? 0;
      },
      set index(val) {
        if (val >= this.ranges.length) val = this.ranges.length - 1;
        if (val < 0) val = 0;
        this._index = val;
        return true;
      },
      get selected() {
        return this.ranges[this.index];
      },
      ranges: [],
      result: []
    },
    mark: {
      id: null,
      type: null,
      mark: null
    },
    countdown: {
      work: false,
      time: 0,
      progress: 0,
      _timeout: null,
      activate(time) {
        clearTimeout(this._timeout);
        this.work = true;
        this.time = 0;
        this.progress = 0;
        setTimeout(() => {
          this.time = time;
          this.progress = 100;
        }, 0);
        return new Promise(res => {
          this._timeout = setTimeout(() => {
            this.disable();
            res();
          }, time);
        });
      },
      disable() {
        clearTimeout(this._timeout);
        this._timeout = 0;
        this.work = false;
        this.time = 0;
      }
    }
  }),
  computed: {
    waitTime() {
      return this.$store.state.settings.storage.diaryWaitTime;
    },
    includeSunday() {
      return this.$store.state.settings.storage.noIncludeSunday;
    },
    showLoader() {
      return this.loading || this.week.loading;
    },
    disableInput() {
      return this.loading || this.week.loading;
    }
  },
  watch: {
    $route() {
      this.loadMark();
    },
    "week.index"(value, old) {
      if (this.loading || !old) return;
      this.$store.dispatch("saveCache", {
        name: "SelectedWeek",
        value
      });
    }
  },
  methods: {
    initView() {
      this.loading = true;
      this.week.index = 0;
      this.week.ranges = [];
      this.week.result = [];
      this.countdown.disable();
      this.week.loading = false;

      return this.$store
        .dispatch("GetStudyYear")
        .then(({ start, end }) => {
          start = new Date(start);
          end = new Date(end);
          while (start < end) {
            const range = {
              start: new Date(start),
              end: null
            };
            while (start.getDay() != 0) {
              start.setDate(start.getDate() + 1);
            }
            range.end = new Date(start);
            this.week.ranges.push(range);
            start.setDate(start.getDate() + 1);
          }
        })
        .then(() => {
          for (let index in this.week.ranges) {
            const { start, end } = this.week.ranges[index];
            if (Date.now() < start || Date.now() > +end + 864e5) continue;
            if (!this.includeSunday && new Date().getDay() == 0) index++;
            return index;
          }
        })
        .then(index => {
          this.loading = false;
          return this.$store.dispatch("getCache", {
            time: 12e4,
            name: "SelectedWeek",
            next: () => index
          });
        })
        .then(index => {
          this.week.index = index;
          this.loadWeek();
        })
        .catch(err => this.$root.$emit("catch-error", err));
    },
    loadWeek() {
      if (!this.week.selected?.start || !this.week.selected?.end) return;
      this.week.loading = true;

      return this.$store
        .dispatch("GetDiaryRange", this.week.selected)
        .then(result => (this.week.result = result))
        .catch(err => this.$root.$emit("catch-error", err))
        .finally(() => (this.week.loading = false));
    },
    loadMark() {
      const { id, type, mark } = this.$route.params;
      if (this.$route.path != "/diary/mark") {
        this.mark.type = null;
        this.mark.mark = null;
        this.mark.id = null;
        return;
      }
      if (!id || !type || !mark) {
        this.$router.replace("/diary");
        return;
      }
      this.mark.type = type;
      this.mark.mark = mark;
      this.mark.id = id;
    }
  },
  created() {
    this.$root.$on("user-changed", this.initView);
    this.loadMark();
    this.initView();
  },
  destroyed() {
    this.countdown.disable();
  },
  components: {
    Day,
    Mark
  }
};
</script>

<style>
.diary {
  position: relative;
}

.diary > .loader,
.diary > .countdown {
  top: 0;
  left: 0;
  width: 100%;
  position: absolute;
}
.diary > .countdown > .md-progress-bar-fill {
  transition: var(--time) linear !important;
}

.diary > .select-week {
  width: 600px;
  display: grid;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  grid-column-gap: 10px;
  grid-template-areas:
    "input input input input"
    "button-1 none none-1 button-2";
}
@media screen and (min-width: 610px) {
  .diary > .select-week {
    grid-template-columns: 88px 400px 88px;
    grid-template-areas: "button-1 input button-2";
  }
  .diary > .select-week > .md-button {
    margin-left: 0;
    margin-right: 0;
  }
}
.diary > .select-week > .md-field {
  grid-area: input;
  margin-bottom: 15px;
}
.diary > .select-week > .md-button {
  align-self: center;
}
.diary > .select-week > .md-button:first-child {
  grid-area: button-1;
}
.diary > .select-week > .md-button:last-child {
  grid-area: button-2;
}

.diary > .result {
  display: flex;
  flex-wrap: wrap;
  margin-top: 10px;
  justify-content: space-around;
}
</style>
