<template>
  <md-card class="diary-day">
    <md-card-header>
      <span class="md-title">{{ day.date | date }}</span>
    </md-card-header>

    <md-card-content>
      <Lesson
        v-for="lesson in day.lessons"
        :key="lesson.id"
        :name="lesson.name"
        :room="lesson.room"
        :number="lesson.number"
        :endTime="lesson.endTime"
        :homework="lesson.homework"
        :startTime="lesson.startTime"
        :assignments="lesson.assignments"
      />
    </md-card-content>
  </md-card>
</template>

<script>
import Lesson from "./DiaryLesson";

export default {
  props: ["day"],
  filters: {
    date(val) {
      if (typeof val == "string") val = new Date(Date.parse(val));
      const week = ["Вс", "Пн", "Вт", "Ср", "Чт", "Пт", "Сб"];
      const month = [
        "янв",
        "фев",
        "мар",
        "апр",
        "май",
        "июн",
        "июл",
        "авг",
        "сен",
        "окт",
        "ноя",
        "дек"
      ];
      return (
        `${week[val.getDay()]}, ` +
        `${val.getDate()} ` +
        `${month[val.getMonth()]}. ` +
        `${val.getFullYear()} г.`
      );
    }
  },
  components: {
    Lesson
  }
};
</script>

<style>
.diary-day {
  width: 100%;
  margin-bottom: 14px;
}
@media screen and (min-width: 600px) and (max-width: 800px) {
  .diary-day {
    width: 100%;
    margin-bottom: 5vw;
  }
}
@media screen and (min-width: 800px) {
  .diary-day {
    width: calc(47vw - 2 * 14px);
    margin: 1.5vw;
  }
}

.diary-day > .md-card-header {
  text-align: center;
}
</style>
