import Vue from "vue";
import App from "./App.vue";
import store from "./store";
import router from "./router";
import Vuelidate from "vuelidate";
import VueMaterial from "vue-material";
import VueNativeSock from "vue-native-websocket";

import "vue-material/dist/theme/default.css";
import "vue-material/dist/vue-material.min.css";

// Всякие модули
Vue.use(Vuelidate);
Vue.use(VueMaterial);
Vue.use(VueNativeSock, "/", {
  store,
  reconnection: true,
  connectManually: true
});

// Всякие фильтры
Vue.filter("date", function(val) {
  if (!val) return "";
  if (typeof val == "string") val = new Date(val);
  function num(val) {
    if (val < 10) val = "0" + val;
    return val;
  }
  return `${num(
    val.getDate()
  )}.${num(val.getMonth() + 1)}.${num(val.getFullYear())}`;
});

// Стили для vue-material
Vue.material.locale.dateFormat = "dd.MM.yyyy";
Vue.material.locale.months = [
  "Январь",
  "Февраль",
  "Март",
  "Апрель",
  "Май",
  "Июнь",
  "Июль",
  "Август",
  "Сентябрь",
  "Октябрь",
  "Ноябрь",
  "Декабрь"
];
Vue.material.locale.shortMonths = [
  "Янв",
  "Фев",
  "Мар",
  "Апр",
  "Май",
  "Июн",
  "Июл",
  "Авг",
  "Сен",
  "Окт",
  "Ноя",
  "Дек"
];
Vue.material.locale.shorterMonths = [
  "Я",
  "Ф",
  "М",
  "Ап",
  "М",
  "Ин",
  "Ил",
  "Ав",
  "С",
  "О",
  "Н",
  "Д"
];
Vue.material.locale.days = [
  "Воскресенье",
  "Понедельник",
  "Вторник",
  "Среда",
  "Четверг",
  "Пятница",
  "Суббота"
];
Vue.material.locale.shortDays = [
  "Вск",
  "Пнд",
  "Втр",
  "Срд",
  "Чтв",
  "Птн",
  "Сбт"
];
Vue.material.locale.shorterDays = ["Вс", "Пн", "Вт", "Ср", "Чт", "Пт", "Сб"];
Vue.material.locale.firstDayOfAWeek = 1;
Vue.material.locale.confirm = "Готово";
Vue.material.locale.cancel = "Отмена";

new Vue({
  store,
  router,
  render: h => h(App)
}).$mount("#app");
