<template>
  <div class="subject">
    <transition name="fade">
      <md-progress-bar
        v-if="showLoader"
        md-mode="query"
        class="loader"
        key="1"
      ></md-progress-bar>
    </transition>
    <md-autocomplete
      class="input-subject"
      :value="subject.input"
      @md-changed="checkExist"
      :disabled="disabledInput"
      :md-options="subject.list"
    >
      <label>Предмет</label>
      <template slot="md-autocomplete-item" slot-scope="{ item }">{{
        item.name
      }}</template>
    </md-autocomplete>
    <transition name="fade">
      <dir class="input-range" v-if="showRange">
        <md-datepicker v-model="subject.range.start" />
        <md-datepicker v-model="subject.range.end" />
      </dir>
    </transition>
    <transition name="fade">
      <md-button
        v-if="showRange"
        @click="loadSubject"
        :disabled="disabledInput"
        class="md-raised md-primary"
        >Загрузить</md-button
      >
    </transition>
    <transition name="fade">
      <md-table
        md-card
        class="result md-scrollbar"
        v-model="subject.result.assignments"
        v-if="subject.result.assignments.length"
      >
        <md-table-row slot="md-table-row" slot-scope="{ item }">
          <md-table-cell
            md-numeric
            :md-label="`Оценка (${subject.result.middleMark})`"
            >{{ item.mark }}</md-table-cell
          >
          <md-table-cell md-label="Тип задания" class="type">{{
            item.type
          }}</md-table-cell>
          <md-table-cell md-label="Тема задания" class="theme">{{
            item.name
          }}</md-table-cell>
          <md-table-cell md-numeric md-label="Дата выполнения">{{
            item.date | date
          }}</md-table-cell>
          <md-table-cell md-numeric md-label="Дата выставления">{{
            item.issueDate | date
          }}</md-table-cell>
        </md-table-row>
      </md-table>
    </transition>
  </div>
</template>

<script>
export default {
  data: () => ({
    loading: true,
    studyYear: {
      start: null,
      end: null
    },
    subject: {
      loading: false,
      list: [],
      input: "",
      selected: null,
      range: {
        start: null,
        end: null
      },
      result: {
        middleMark: 0,
        assignments: []
      }
    }
  }),
  computed: {
    showLoader() {
      return this.loading || this.subject.loading;
    },
    disabledInput() {
      return this.loading || this.subject.loading;
    },
    showRange() {
      return !!this.subject.selected;
    }
  },
  watch: {
    "subject.range": {
      handler(value) {
        if (!value.start || !value.end) return;
        this.$store.state.settings.storage.subjectDates[
          this.subject.selected
        ] = JSON.parse(JSON.stringify(value));
        this.$store.dispatch("saveSettings");
      },
      deep: true
    },
    "subject.selected"(value) {
      this.$store.dispatch("saveCache", {
        name: "selectedSubject",
        value
      });
      const range = this.$store.state.settings.storage.subjectDates[value];
      this.subject.range.start = new Date(range?.start ?? this.studyYear.start);
      this.subject.range.end = new Date(range?.end ?? this.studyYear.end);
    }
  },
  methods: {
    initView() {
      this.loading = true;
      this.subject.list = [];
      this.subject.input = "";
      this.subject.selected = null;
      this.subject.result.middleMark = 0;
      this.subject.result.assignments = [];

      return this.$store
        .dispatch("GetStudyYear")
        .then(({ start, end }) => {
          this.studyYear.start = new Date(start);
          this.studyYear.end = new Date(end);
          return this.$store.dispatch("GetSubjectsList");
        })
        .then(list => {
          this.subject.list = list;
          return this.$store.dispatch("getCache", {
            time: 12e4,
            name: "selectedSubject",
            next: () => null
          });
        })
        .then(selected => {
          this.subject.selected = selected;
          this.loading = false;

          if (this.$route.path == "/subject/id") {
            if (!this.$route.params.id) this.$router.replace("/subject");
            this.subject.selected = this.$route.params.id;
          }
          if (this.subject.selected) {
            this.subject.input = this.subject.list.find(
              s => s.value == this.subject.selected
            ).title;
            setTimeout(() => this.loadSubject(), 0);
          }
        })
        .catch(err => this.$root.$emit("catch-error", err));
    },
    checkExist(str) {
      const select = this.subject.list.find(s => s.name == str);
      if (select) this.subject.selected = select.id;
      else this.subject.selected = null;
    },
    loadSubject() {
      this.subject.loading = true;

      return this.$store
        .dispatch("GetSubjectReport", {
          id: this.subject.selected,
          end: this.subject.range.end,
          start: this.subject.range.start
        })
        .then(subject => {
          this.subject.result.middleMark = subject.middleMark;
          this.subject.result.assignments = subject.assignments.sort(
            (a, b) => -new Date(b.date) - new Date(a.date)
          );
        })
        .catch(err => this.$root.$emit("catch-error", err))
        .finally(() => (this.subject.loading = false));
    }
  },
  mounted() {
    document.addEventListener(
      "click",
      e => (
        e.target.closest("td.md-table-cell.theme")?.classList.toggle("show"),
        true
      )
    );
    this.$root.$on("user-changed", this.initView);
    this.initView();
  },
  destroyed() {
    document.removeEventListener("click", e =>
      e.target.closest("td.md-table-cell.theme")?.classList.toggle("show")
    );
  }
};
</script>

<style>
.subject {
  position: relative;
}

.subject > .loader {
  top: 0;
  left: 0;
  width: 100%;
  position: absolute;
}

.subject .fade-enter-active,
.subject .fade-leave-active {
  overflow: visible !important;
  max-height: 76px !important;
  transition: all 350ms ease-in-out !important;
}
.subject .fade-enter,
.subject .fade-leave-to {
  margin: 0 !important;
  opacity: 0 !important;
  padding: 0 !important;
  max-height: 0 !important;
  transform: scale(0) translateY(100%) !important;
}

.subject > .input-subject {
  width: 400px;
  max-width: 100%;
}

.subject > .input-range {
  padding: 0;
  width: 400px;
  display: flex;
  max-width: 100%;
  flex-direction: row;
  justify-content: space-between;
}
.subject > .input-range > .md-datepicker {
  width: 45%;
  overflow: hidden;
}

.subject > .result {
  margin-top: 30px;
}
.subject > .result td.md-numeric div {
  text-align: center;
}
.subject > .result td.type div {
  white-space: nowrap;
}
.subject > .result td.theme div {
  cursor: pointer;
  max-width: 200px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.subject > .result td.theme.show div {
  width: 400px;
  max-width: unset;
  white-space: normal;
}
</style>
