<template>
  <div class="user-info" v-if="show">
    <img :src="photo" alt="Фото профиля" />
    <div>
      <span class="md-body-1">{{ firstName }} {{ lastName }}</span>
      <span class="md-caption">{{ email }}</span>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";

export default {
  props: ["id", "cache"],
  data: () => ({
    show: false,
    photo: "",
    email: "",
    lastName: "",
    firstName: ""
  }),
  watch: {
    id() {
      this.loadInfo();
    }
  },
  methods: {
    ...mapActions(["getCache", "saveCache"]),
    loadInfo() {
      this.show = false;
      if (!this.id) return;
      this.getCache({
        time: this.cache === false ? 0 : 864e5,
        common: true,
        name: `user-${this.id}`,
        next: () =>
          fetch(`//${location.hostname}/users/${this.id}`)
            .then(res => {
              if (!res.ok) throw null;
              return res.json();
            })
            .then(value =>
              this.saveCache({ common: true, name: `user-${this.id}`, value })
            )
      })
        .then(data => {
          this.show = true;
          this.photo = data.photo;
          this.email = data.email;
          this.lastName = data.lastName;
          this.firstName = data.firstName;
        })
        .catch(err => {
          this.$emit("error", err);
          console.error(err);
        });
    }
  },
  mounted() {
    this.loadInfo(this.id);
  }
};
</script>

<style scoped>
div.user-info {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
}
div.user-info > img {
  width: 40px;
  height: 40px;
  margin-top: 4px;
  overflow: hidden;
  object-fit: cover;
  margin-left: 10px;
  border-radius: 50%;
  margin-bottom: 4px;
  object-position: center;
}
div.user-info > div {
  display: flex;
  align-items: flex-end;
  flex-direction: column;
}

div.user-info.revers {
  flex-direction: row;
}
div.user-info.revers > img {
  margin-left: 0;
  margin-right: 10px;
}
div.user-info.revers > div {
  align-items: flex-start;
}
</style>
