import Vue from "vue";

class Guid {
  constructor() {
    this._genHistory = [];
  }

  genID() {
    let id;
    do {
      id = "xxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx".replace(/[xy]/g, c => {
        var r = (Math.random() * 16) | 0,
          v = c == "x" ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      });
    } while (this._genHistory.includes(id));
    this._genHistory.push(id);
    return id;
  }

  checkID(id) {
    return this._genHistory.includes(id);
  }
}
const messageGuid = new Guid();
class SocketRequest {
  constructor(obj) {
    this._id = messageGuid.genID();
    this._send = false;
    this._message = obj;
    this._callbackDone = obj?.callback?.done;
    this._callbackError = obj?.callback?.error;
  }

  get id() {
    return this._id;
  }

  get send() {
    return this._send;
  }
  set send(val) {
    return (this._send = val);
  }

  get message() {
    return JSON.stringify({
      mid: this._id,
      data: this._message.data,
      method: this._message.method
    });
  }

  get rawMessage() {
    return {
      mid: this._id,
      method: this._message.method,
      data: this._message.data,
      timestamp: this._timestamp
    };
  }

  callback(msg) {
    const error = "error" in msg;
    this[error ? "_callbackError" : "_callbackDone"]?.call(
      this,
      msg[error ? "error" : "data"]
    );
  }
}

export default {
  state: {
    isConnected: false,
    messages: [],
    ping: 86
  },
  actions: {
    // Cache: off
    CheckHost({ dispatch }, host) {
      return dispatch("SendMessage", {
        method: "CheckHost",
        data: { host }
      }).then(({ fit }) => fit);
    },
    GetAuthForm({ dispatch }, host) {
      return dispatch("SendMessage", {
        method: "GetAuthForm",
        data: { host }
      }).then(({ form }) => form);
    },
    UploadAuthForm({ dispatch }, data) {
      return dispatch("SendMessage", {
        method: "UploadAuthForm",
        data
      }).then(({ items }) => items);
    },
    AddNewUser({ dispatch }, data) {
      return dispatch("SendMessage", {
        method: "AddNewUser",
        data
      }).then(({ tid }) => tid);
    },
    LogInViaLink({ dispatch }, id) {
      return dispatch("SendMessage", {
        method: "LogInViaLink",
        data: { id }
      }).then(({ tid }) => tid);
    },
    ChangeUser({ dispatch }, id) {
      return dispatch("SendMessage", {
        method: "ChangeUser",
        data: { id }
      }).then(({ changed }) => changed);
    },
    GetAuthLink({ dispatch }, id) {
      return dispatch("SendMessage", {
        method: "GetAuthLink",
        data: { id }
      }).then(({ link }) => link);
    },
    CheckRights({ dispatch }) {
      return dispatch("SendMessage", {
        method: "CheckRights"
      }).then(({ can }) => can);
    },
    ChangeRestric({ dispatch }, data) {
      return dispatch("SendMessage", {
        method: "ChangeRestric",
        data
      }).then(({ limited }) => limited);
    },
    RemoveCurrentUser({ dispatch }) {
      return dispatch("SendMessage", {
        method: "RemoveCurrentUser"
      }).then(({ deleted }) => deleted);
    },
    ConnectedUsers({ dispatch }) {
      return dispatch("SendMessage", {
        method: "ConnectedUsers"
      }).then(({ users }) => users);
    },
    RemoveConnectedUser({ dispatch }, id) {
      return dispatch("SendMessage", {
        method: "RemoveCurrentUser",
        data: { id }
      }).then(({ deleted }) => deleted);
    },
    CheckAuth({ dispatch }) {
      return dispatch("SendMessage", {
        method: "CheckAuth"
      }).then(({ need }) => need);
    },
    LeftSessionTime({ dispatch }) {
      return dispatch("SendMessage", {
        method: "LeftSessionTime"
      }).then(({ time }) => time);
    },
    LogIn({ dispatch }) {
      return dispatch("SendMessage", {
        method: "LogIn"
      }).then(({ auth }) => auth);
    },
    LogOut({ dispatch }) {
      return dispatch("SendMessage", {
        method: "LogOut"
      }).then(({ auth }) => auth);
    },
    // Cache: on
    GetAnnouncements({ dispatch }) {
      return dispatch("getCache", {
        time: 6e5,
        name: "announcements",
        next: () =>
          dispatch("AuthBefore", {
            method: "GetAnnouncements"
          }).then(value =>
            dispatch("saveCache", { name: "announcements", value })
          )
      }).then(({ announcements }) => announcements);
    },
    GetAnnouncementsID({ dispatch }) {
      return dispatch("getCache", {
        time: 6e4,
        name: "announcementsID",
        next: () =>
          dispatch("AuthBefore", {
            method: "GetAnnouncementsID"
          }).then(value =>
            dispatch("saveCache", { name: "announcementsID", value })
          )
      }).then(({ ids }) => ids);
    },
    GetUnreadedMessages({ dispatch }) {
      return dispatch("getCache", {
        time: 6e4,
        name: "unreadedMessages",
        next: () =>
          dispatch("AuthBefore", {
            method: "GetUnreadedMessages"
          }).then(value =>
            dispatch("saveCache", { name: "unreadedMessages", value })
          )
      }).then(({ number }) => number);
    },
    GetBirthdays({ dispatch }, date) {
      const name = "birthdays" + date;
      return dispatch("getCache", {
        time: 864e5,
        name,
        next: () =>
          dispatch("AuthBefore", {
            data: {
              date
            },
            method: "GetBirthdays"
          }).then(value => dispatch("saveCache", { name, value }))
      }).then(({ birthdays }) => birthdays);
    },
    GetStudyYear({ dispatch }) {
      return dispatch("getCache", {
        time: 864e5,
        name: "studyYear",
        next: () =>
          dispatch("AuthBefore", {
            method: "GetStudyYear"
          }).then(value => dispatch("saveCache", { name: "studyYear", value }))
      }).then(({ studyYear }) => studyYear);
    },
    GetAssignmentsType({ dispatch }) {
      return dispatch("getCache", {
        time: 864e5,
        name: "assignmentsType",
        next: () =>
          dispatch("AuthBefore", {
            method: "GetAssignmentsType"
          }).then(value =>
            dispatch("saveCache", { name: "assignmentsType", value })
          )
      }).then(({ types }) => types);
    },
    GetDiaryRange({ dispatch }, data) {
      const name = `DiaryRange${data.start}${data.end}`;
      return dispatch("getCache", {
        name,
        time: 12e4,
        next: () =>
          dispatch("AuthBefore", {
            method: "GetDiaryRange",
            data
          }).then(value => dispatch("saveCache", { name, value }))
      }).then(({ days }) => days);
    },
    GetAssignment({ dispatch }, id) {
      const name = `Assignment${id}`;
      return dispatch("getCache", {
        name,
        time: 864e5,
        next: () =>
          dispatch("AuthBefore", {
            method: "GetAssignment",
            data: { id }
          }).then(value => dispatch("saveCache", { name, value }))
      }).then(({ assignment }) => assignment);
    },
    GetSubjectsList({ dispatch }) {
      return dispatch("getCache", {
        time: 864e5,
        name: "subjectsList",
        next: () =>
          dispatch("AuthBefore", {
            method: "GetSubjectsList"
          }).then(value =>
            dispatch("saveCache", { name: "subjectsList", value })
          )
      }).then(({ subjectsList }) => subjectsList);
    },
    GetSubjectReport({ dispatch }, data) {
      const name = `Subject${data.id}${data.start}${data.end}`;
      return dispatch("getCache", {
        name,
        time: 12e4,
        next: () =>
          dispatch("AuthBefore", {
            method: "GetSubjectReport",
            data
          }).then(value => dispatch("saveCache", { name, value }))
      }).then(({ subject }) => subject);
    },
    GetStudyReport({ dispatch }, data) {
      const name = `Journal${data.start}${data.end}`;
      return dispatch("getCache", {
        name,
        time: 12e4,
        next: () =>
          dispatch("AuthBefore", {
            method: "GetStudyReport",
            data
          }).then(value => dispatch("saveCache", { name, value }))
      }).then(({ journal }) => journal);
    },
    GetTotalReport({ dispatch }) {
      return dispatch("getCache", {
        name: "TotalReport",
        time: 12e4,
        next: () =>
          dispatch("AuthBefore", {
            method: "GetStudyReport"
          }).then(value =>
            dispatch("saveCache", { name: "TotalReport", value })
          )
      });
    },
    // System: true
    Ping({ dispatch }) {
      setTimeout(
        () =>
          dispatch("SendMessage", {
            method: "Ping",
            data: {
              timestamp: Date.now()
            }
          }),
        5000
      );
    },
    AuthBefore({ dispatch }, obj) {
      return dispatch("CheckAuth")
        .then(need => (need ? dispatch("LogIn") : void 0))
        .then(() => dispatch("SendMessage", obj));
    },
    SendMessage({ state, commit }, obj) {
      return new Promise((done, error) => {
        obj.callback = {
          done,
          error
        };

        const req = new SocketRequest(obj);
        req.send = state.isConnected;
        if (state.isConnected) Vue.prototype.$socket.send(req.message);
        commit("addMessage", req);
      });
    }
  },
  mutations: {
    addMessage(state, req) {
      state.messages.push(req);
    },
    SOCKET_ONOPEN(state) {
      this.dispatch("Ping");
      state.isConnected = true;
      for (let message of state.messages) {
        if (message.send) continue;
        if (!state.isConnected) return;
        message.send = true;
        Vue.prototype.$socket.send(message.message);
      }
    },
    SOCKET_ONCLOSE(state) {
      state.isConnected = false;
    },
    SOCKET_ONERROR(state, event) {
      console.group("SOCKET_ONERROR");
      console.info("State: ", state);
      console.error("Event: ", event);
      console.groupEnd();
    },
    SOCKET_ONMESSAGE(state, message) {
      try {
        message = JSON.parse(message.data);
      } catch (e) {
        console.error("The message couldn't be processed.", e);
        return false;
      }
      if (message.method == "Ping") {
        state.ping = Date.now() - message.data.timestamp;
        this.dispatch("Ping");
      }
      if (!message.mid) return;
      const req = state.messages.find(m => message.mid === m.id);
      req.callback(message);
    },
    SOCKET_RECONNECT(state, count) {
      console.group("SOCKET_RECONNECT");
      console.info("Count: ", count);
      console.info("Messages: ", state.messages);
      console.info("Listeners: ", state.listeners);
      console.groupEnd();
    }
  }
};
