<template>
  <div class="toolbar">
    <md-toolbar>
      <md-badge
        class="md-primary"
        :md-content="totalCount"
        :is="!totalCount ? 'div' : 'md-badge'"
      >
        <md-button class="md-icon-button" @click="showNavigation = true">
          <md-icon>menu</md-icon>
        </md-button>
      </md-badge>
      <h3 class="md-title" style="flex: 1">{{ title || "Загрузка..." }}</h3>
      <md-button class="user-button" @click="showUsers = true">
        <UserInfo :id="uid" />
      </md-button>
    </md-toolbar>

    <md-drawer
      class="md-scrollbar"
      :md-active.sync="showNavigation"
      md-swipeable
    >
      <md-list>
        <md-list-item to="/announcements">
          <md-icon>dashboard</md-icon>
          <span class="md-list-item-text">Объявления</span>
          <md-badge
            class="md-primary"
            v-if="announcements"
            :md-content="announcements"
          />
        </md-list-item>

        <md-list-item to="/birthdays">
          <md-icon>cake</md-icon>
          <span class="md-list-item-text">Дни рождения</span>
          <md-badge
            class="md-primary"
            v-if="birthdayCount"
            :md-content="birthdayCount"
          />
        </md-list-item>

        <md-list-item hidden>
          <md-icon>today</md-icon>
          <span class="md-list-item-text">Расписание</span>
        </md-list-item>

        <md-list-item to="/diary">
          <md-icon>assignment</md-icon>
          <span class="md-list-item-text">Дневник</span>
        </md-list-item>

        <md-list-item hidden>
          <md-icon>email</md-icon>
          <span class="md-list-item-text">Почта</span>
          <md-badge
            class="md-primary"
            v-if="unreadedMessages"
            :md-content="unreadedMessages"
          />
        </md-list-item>

        <md-subheader>Итоги успеваемости</md-subheader>

        <md-list-item to="/grade">
          <md-icon>school</md-icon>
          <span class="md-list-item-text">Итоговые отметки</span>
        </md-list-item>

        <md-list-item hidden>
          <md-icon>linear_scale</md-icon>
          <span class="md-list-item-text">Средний балл</span>
        </md-list-item>

        <md-list-item hidden>
          <md-icon>trending_up</md-icon>
          <span class="md-list-item-text">Динамика среднего балла</span>
        </md-list-item>

        <md-subheader>Текущая успеваемость и посещаемость</md-subheader>

        <md-list-item to="/subject">
          <md-icon>subject</md-icon>
          <span class="md-list-item-text">Отчет об успеваемости</span>
        </md-list-item>

        <md-list-item to="/journal">
          <md-icon>leaderboard</md-icon>
          <span class="md-list-item-text"
            >Отчет об успеваемости и посещаемости</span
          >
        </md-list-item>

        <md-list-item hidden>
          <md-icon>star</md-icon>
          <span class="md-list-item-text">Успеваемость и качество знаний</span>
        </md-list-item>

        <md-list-item hidden>
          <md-icon>edit</md-icon>
          <span class="md-list-item-text">Отчет о доступе к журналу</span>
        </md-list-item>

        <md-list-item hidden>
          <md-icon>attach_email</md-icon>
          <span class="md-list-item-text">Письмо для родителей</span>
        </md-list-item>

        <md-subheader>Система</md-subheader>

        <md-list-item to="/settings">
          <md-icon>settings</md-icon>
          <span class="md-list-item-text">Настройки</span>
        </md-list-item>

        <md-list-item @click="darkMode = 0">
          <md-icon>invert_colors</md-icon>
          <span class="md-list-item-text">Тёмная тема</span>
          <md-switch v-model="darkMode" />
        </md-list-item>
      </md-list>
    </md-drawer>

    <md-drawer
      class="md-scrollbar"
      :md-active.sync="showUsers"
      md-swipeable
      md-right
    >
      <md-list>
        <md-list-item>
          <md-icon>add_circle_outline</md-icon>
          <span class="md-list-item-text">Добавить пользователя</span>
        </md-list-item>
        <md-list-item
          :key="id"
          v-for="id in users"
          @click="
            id == uid ? (showUsers = false) : $root.$emit('change-user', id)
          "
        >
          <UserInfo :id="id" class="revers" />
          <md-icon v-if="id == uid" class="md-primary">stars</md-icon>
        </md-list-item>
      </md-list>
    </md-drawer>
  </div>
</template>

<script>
import { mapState } from "vuex";
import UserInfo from "../components/UserInfo";

export default {
  props: ["title"],
  data: () => ({
    interval: null,
    showUsers: false,
    showNavigation: false,
    birthdayCount: 0,
    unreadedMessages: 0,
    allAnnouncements: []
  }),
  computed: {
    ...mapState({
      users: state => state.settings.storage.connectedUsers
    }),
    uid: {
      get() {
        return this.$store.state.settings.storage.uid;
      },
      set() {
        this.$root.$emit("change-theme");
        return true;
      }
    },
    darkMode: {
      get() {
        return this.$store.state.settings.storage.darkMode;
      },
      set() {
        this.$root.$emit("change-theme");
        return true;
      }
    },
    totalCount() {
      return this.announcements; // + this.unreadedMessages + this.birthdayCount;
    },
    announcements() {
      let i = 0;
      this.allAnnouncements.forEach(id => {
        if (!this.$store.state.settings.storage.readAnnouncements.includes(id))
          i++;
      });
      return i;
    }
  },
  watch: {
    $route() {
      this.showNavigation = false;
    }
  },
  methods: {
    updateValue() {
      const dispatch = this.$store.dispatch;
      dispatch("GetAnnouncementsID")
        .then(ids => (this.allAnnouncements = ids))
        .catch(err => this.$root.$emit("catch-error", err));

      dispatch("GetUnreadedMessages")
        .then(number => (this.unreadedMessages = number))
        .catch(err => this.$root.$emit("catch-error", err));
    }
  },
  mounted() {
    this.$root.$on("user-changed", () => {
      this.showUsers = false;
      this.updateValue();
    });
    this.interval = setInterval(() => this.updateValue(), 6e4);
    this.updateValue();
  },
  destroyed() {
    clearInterval(this.interval);
  },
  components: {
    UserInfo
  }
};
</script>

<style>
.toolbar {
  top: 0;
  left: 0;
  z-index: 10;
  position: sticky;
}
.toolbar .user-button {
  height: fit-content;
  text-transform: none;
}
.toolbar .md-drawer {
  width: fit-content;
  z-index: 11;
  position: fixed;
  min-width: 300px;
}
.toolbar .md-overlay {
  z-index: 10;
  position: fixed;
}
</style>
